<template>
  <header class="header">
    <div class="container header__container">
      <a href="#" class="logo header__logo"><img class="logo__img" src="images/logo.png" alt="the Loft"></a>
      <nav class="navigation">
        <ul class="navigation__ul">
          <li class="navigation__li"><a href="" class="navigation__link">О НАС</a></li>
          <li class="navigation__li"><a href="" class="navigation__link">МЕРОПРИЯТИЯ</a></li>
          <li class="navigation__li"><a href="" class="navigation__link">РАСПИСАНИЕ</a></li>
          <li class="navigation__li"><a href="" class="navigation__link">КОНТАКТЫ</a></li>
          <li class="navigation__li"><a href="" class="navigation__link">БЛОГ</a></li>
        </ul>
      </nav>
      <div class="socials">
        <a href="#" class="socials__link"><i class="fa fa-vk" aria-hidden="true"></i></a>
        <a href="#" class="socials__link"><i class="fa fa-youtube" aria-hidden="true"></i></a>
        <a href="#" class="socials__link"><i class="fa fa-instagram" aria-hidden="true"></i></a>
      </div>
    </div>
  </header>
</template>
<script>

export default {
  name: 'LoftHeader',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
$textColor:#cccccc;
$accentColor: #eab156;
$white: #fff;
.header {
    position: fixed;
    width: 100%;
    z-index: 20;
    &__container {
        display: flex;
        align-items: baseline; // justify-content: space-between;
    }
    &__logo {
        position: relative;
        top: 13px;
    }
}

.logo{
    &__img{
        width: 131px;
        height: 51px;
    }
}

.navigation{
        margin: 0 auto;
    &__ul{
        display: flex;
    }
    &__li{
        &+&{
            margin-left: 55px;
        }
    }
    &__link{
        font-size: 12px;    
        color:$white;
        text-transform: uppercase;
        transition: color .3s ease-in-out;
        &:hover{
            color:$accentColor;
        }
    }
}
</style>



